@import url(https://fonts.googleapis.com/css?family=Nunito);
@import url('https://fonts.googleapis.com/css?family=Orbitron:400,500&display=swap');

body {
  background: #000;
  font-family: "Droid Sans Mono",Menlo,Monaco,Consolas,"Liberation Mono","Courier New",monospace;
  color: #FFF;
  overflow-x:hidden;
}
header {
  padding:2em;
  display:flex;
  flex-wrap:wrap;
  justify-content:space-between;
  border-bottom: 1px solid rgba(48,52,60,0.75);
}
iframe {
  max-width: 100%;
  margin: 0 auto;
}
::placeholder {
  opacity: .5;
}
.top-menu ul {
  display:flex;
  flex-wrap:wrap;
}
.top-menu ul li {
  margin:0 10px;
}
#form {
  /* padding: 2em; */
  text-align:center;
  border-bottom: 1px solid rgba(48,52,60,0.75);
}
.wrap {
  padding: 20px 0;
  margin: 0 auto;
  width: 980px;
  flex-direction: row;
  display: block;
  margin-left: auto;
  margin-right: auto;
  visibility:hidden;

}
.wrap button {
  font-weight: bolder;
  font-size: 22px;
  background: #000;
  color: #0ebeff;
  border: 1px solid;
  width: 100%;
  cursor: pointer;
  padding :0 5px;
  line-height: 40px;
  -webkit-transition: background-color .2s ease-out;
  -moz-transition: background-color .2s ease-out;
  -o-transition: background-color .2s ease-out;
  transition: background-color .2s ease-out;
}

.wrap button:nth-child(2n+1) {
  margin-right:6px;
} 
.wrap button:hover {
  background-color: #2bcdee29;
}
button#slider-start-next:active, button#slider-next:active {
  transform: translateX(4px);
}
button#slider-start-prev:active, button#slider-end-prev:active {
  transform: translateX(-4px);
}
#ytplayer {
  display: flex;
  flex-flow: column;
  justify-content: center;
  max-width: 1700px;
  margin: 0 auto;
  padding: 2em;
    border-bottom: 1px solid rgba(48,52,60,0.75);
}
#prevtime, #nexttime {
    flex: 1;
    color: #2bcdee;
    font-weight: bolder;
    font-size: 24px;
    color: #2bcdee45;
    cursor: no-drop;
    text-align: center;
    border: 1px solid;
    line-height: 35px;
    -webkit-transition: background-color .2s ease-out;
    -moz-transition: background-color .2s ease-out;
    -o-transition: background-color .2s ease-out;
    transition: background-color .2s ease-out;
}
#prevtime:hover , #nexttime:hover {
    background-color: #2BCDEE14;
}
#prevtime:active {
    transform: translateX(-4px);
}
#nexttime:active {
    transform: translateX(4px);
}

.controls {
  flex-grow: 1;
}
.controls .info {
  margin-bottom: 1px;
  border-bottom: 1px solid #0ebeff;
  padding: 10px 0;
  display:flex;
  justify-content: space-between;
  align-items: center;
}
.controls .info div, h5 {
  margin: .5em 0;
}
#screnShot{
  position: relative;
}
.shadow{
  position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 2;
    display: flex;
    width: 980px;
    flex-direction: row;
    /* display: block; */
    margin-left: auto;
    margin-right: auto;
}
.shadow_left, .shadow_right{
  background-color: rgba(32,57,109,.86);
  height: 100%;
}
/* .js-irs-0 { 
  flex-grow: 1;
  flex-basis:100%;
  margin:0 20px;
}*/
.irs--round .irs-min, .irs--round .irs-max {
  color: #888;
}
.form {
    max-width: 800px;
    height: 80px;
    margin: 0 auto;
    margin-top: 25px;
}
.form label {
    flex-basis: 100%;
    width: 100%;
    margin-bottom: 5px;
}
.form form {
    display: flex;
}
#d-pscreen {
  opacity: .5;
  cursor: no-drop;
}
.btn {
    display: inline-block;
    background: transparent;
    color: inherit;
    font: inherit;
    border: 0;
    outline: 0;
    padding: 0;
    transition: all 200ms ease-in;
    cursor: pointer;
  }
  .btn--primary {
    background: #f39c12;
    color: #fff;
    box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.1);
    border-radius: 2px;
    padding: 12px 10px;
  }
  .btn--primary:hover {
    background: #f89406;
  }
  .btn--primary:active {
    background: #f39c12;
    box-shadow: inset 0 0 10px 2px rgba(0, 0, 0, 0.2);
  }
  .btn--inside {
    position: relative;
    z-index: 10;
    /* margin-left: -96px; */
    margin-left: 0;
    flex-grow: 1;
  }
  
  .form__field {
    min-width: calc(100% - 170px);
    background: #fff;
    color: #a3a3a3;
    font: inherit;
    box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.1);
    border: 0;
    outline: 0;
    padding: 22px 18px;
  }
.download ul {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  text-align: center;
  align-items: center;
  max-width: 750px;
  margin:0 auto;
  color: #c2c4c9;
}
.download ul li:nth-child(odd) span {
  order: 2;
  margin-right: 15px;
}
.download ul li {
    flex: calc(40% - 40px);
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px;
    cursor: pointer;
    /* background-color: darkred; */
    padding: 20px;
    border: 1px solid transparent;
    -webkit-transition: border-color .4s ease;
    -moz-transition: border-color .4s ease;
    -o-transition: border-color .4s ease;
    transition: border-color .4s ease;
}
.download ul li:hover {
    border-color: #2BCDEE;
}

.download img {
    height: 36px;
}
.player-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  margin-top: 15px;
  background-color: #041318;
}
.player-controls div {
  flex-grow: 1;
}
.player-controls div:nth-child(1) {
  display: flex;
  justify-content: flex-start;
}
.player-controls div:nth-child(3) {
  display: flex;
  justify-content: flex-end;
}
.wrap .thumb-0, .wrap .thumb-1{
  width: auto;
}
#time {
    font-family: 'Orbitron', sans-serif;
    background: none;
    border: none;
    color: #FFF;
    /* text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #ff0080, 0 0 30px #ff0080, 0 0 40px #ff0080, 0 0 55px #ff0080, 0 0 75px #ff0080; */
    text-align: center;
    min-width:130px;
    font-size: 16px;
    letter-spacing: 2px;
    padding:0 3px;
    flex: 1;
    cursor: pointer;
}
.preview {
  width: 28%;
  min-width: 200px;
  margin: 0 auto;
  text-align:center;
}
#preview {
    font-size:14px;
    width: auto;
    margin: 10px;
    -webkit-box-sizing: border-box !important;
    -moz-box-sizing: border-box !important;
    -ms-box-sizing: border-box !important;
    box-sizing: border-box !important;
    padding: 3px 0;
    color: #0ebeff;
    cursor: pointer;
    text-transform: uppercase;
    border:1px solid #0ebeff;
    padding: 5px;
    letter-spacing: 1px;
}
#preview:hover {
    background-color: #2BCDEE;
    color: #000!important;
}
@media(min-width:992px) {
  #time {
    font-size: 20px;
    min-width: 160px;
  }
  #ytplayer {
      flex-flow: row;
  }
  iframe {
    max-width: 100%;
  }

  .controls {
      padding: 0 0 0 2em;
  }
  .controls h5 {
    margin-top:0;
  }
   .btn--inside {
        margin-left: -96px;
    }
    .form__field {
        min-width: calc(100% - 96px);
    }
    .form form {
        display: block;
    }
    .btn--primary {
        padding: 12px 36px;
    }
} 
.noselect {
  outline: none;
    -webkit-touch-callout: none; /* iOS Safari */
      -webkit-user-select: none; /* Safari */
       -khtml-user-select: none; /* Konqueror HTML */
         -moz-user-select: none; /* Firefox */
          -ms-user-select: none; /* Internet Explorer/Edge */
              user-select: none; /* Non-prefixed version, currently
                                    supported by Chrome and Opera */
}
#range-slider {
  background: transparent;
  border: 0
}

.chous {
  width: 180px;
  height: 50px;
  border-radius: 4px;
  text-align: center;
  margin: 25px;
  cursor: pointer;
  display: block;
  font: 14px/50px Tahoma;
  transition: all 0.18s ease-in-out;
  border: 1px solid #4FD666;
  background: linear-gradient(to top right, #3EC97A, #69EA49 20%, rgba(255, 255, 255, 0) 80%, rgba(255, 255, 255, 0)) top right/500% 500%;
  color: #4FD666;
}

.chous:hover {
  color: white;
  background-position: bottom left;
}
  
.my {
width: 0.1px;
height: 0.1px;
opacity: 0;
overflow: hidden;
position: absolute;
z-index: -1;
}
#videoCanavas{
  width: 1000px;
  flex-direction: row;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
img,canvas {
  width: 50px;
  height: 100px;
}
.player-controls {
  margin-top: 56px;
}

.wrap{
  position: relative;
  top: -55px;
}
.irs--round .irs-from:before, .irs--round .irs-to:before, .irs--round .irs-single:before {
  opacity: 0;
}
.irs-max .irs-min{
  opacity: 0;
}
.irs-from, .irs-to{
  top: 51px!important;
}
.to :before {    background-repeat: no-repeat;
  background-position: 50%;
  content: " ";
  width: 3px;
  height: 15px;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  margin-top: 0;
  background-image: url('line.svg');
}
.from :before {    background-repeat: no-repeat;
  background-position: 50%;
  content: " ";
  width: 3px;
  height: 15px;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  margin-top: 0;
  background-image: url('line.svg');
}
.wrap .to{
  width: 12px;
    position: absolute;
    top: 0;
    bottom: 0;
    background-color: #006cfa;;
    pointer-events: all;
    cursor: ew-resize;
    box-sizing: border-box;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    border-top-right-radius: 4px;
    height: 109px;
    top: -69px;
    z-index: 3;

}

.thumb:before {
  bottom: 0;
  background-color: #006cfa;
  pointer-events: all;
  cursor: ew-resize;
  box-sizing: border-box;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  border-top-right-radius: 4px;
  height: 109px;
  top: -69px;
  z-index: 3;
  background-repeat: no-repeat;
  /* background-position: 30%; */
  content: " ";
  width: 3px;
  height: 103px;
  position: absolute;
  top: -61px;
  left: 50%;
  transform: translate(-50%,-50%);
  margin-top: 0;
  /* background-image: url(http://localhost:3000/static/media/line.a86a881â€¦.svg); */
  width: 12px;
  top: -58px;
  left: 13%;

}
.wrap .from{
  width: 12px;
    position: absolute;
    top: 0;
    bottom: 0;
    background-color: #006cfa;;
    pointer-events: all;
    cursor: ew-resize;
    box-sizing: border-box;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
    height: 109px;
    top: -69px;
    z-index: 3;    
    transform: translateX(-70%);
}
.irs--round .irs-handle.state_hover, .irs--round .irs-handle:hover {
  background-color: #006cfa;
}

.irs-single {
  top: 51px;
}
#time, #duration {
  margin-left: auto;
  display: block;
  margin-right: auto;
}

.input__wrapper {
  width: 100%;
  position: relative;
  margin: 15px 0;
  text-align: center;
}
 
.input__file {
  opacity: 0;
  visibility: hidden;
  position: absolute;
}
 
.input__file-icon-wrapper {
  height: 60px;
