  counter-increment: myCounter;
  content:counter(myCounter);
  color: white;
  background: #2980B9;
  display: inline-block;
  text-align: center;
  margin: 5px 10px;
  line-height: 40px;
  width: 40px;
  height: 40px;
}
.desc{
  margin-left: auto;
  margin-right: auto;
  width: 300px;
}
.input__file-button {
  width: 100%;
  max-width: 290px;
